.form {
  width: 450px;
}

.input {
  line-height: 20px;
  display: inline-block;
  width: 100%;
  padding-left: 10px;

  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #a9ddff;
  color: #373838;
  font-size: 16px;
  font-weight: 400;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
}

